export default {
	data() {
		return {
			
		}
	},
	methods: {
		barTo: function(e) {
			let name = e.currentTarget.dataset.name
			if (name == "scan") {
				uni.navigateTo({
					url: "/pages/" + name + "/" + name
				})
			} else {
				uni.redirectTo({
					url: "/pages/" + name + "/" + name
				})
			}
		}
	}
}
